
    import {defineComponent} from 'vue'
    import {PlusOutlined,MinusOutlined} from '@ant-design/icons-vue';
    import dynamicInput from './js/index';
    export default defineComponent({
        components:{
            PlusOutlined,
            MinusOutlined
        },
        emits:['change'],
        setup(props,context){
            const {
                inputBoxDom,
                data,
                addInput,
                ItemInputAddBtn,
                ItemInputDelBtn,
                inputBlur,
            } = dynamicInput(props,context);

            return {
                inputBoxDom,
                data,
                addInput,
                ItemInputAddBtn,
                ItemInputDelBtn,
                inputBlur,
            }
        }
    })
